import React from 'react';
import PropTypes from 'prop-types';

import { Group, Panel, Button, PanelHeader, Div, PanelHeaderBack } from '@vkontakte/vkui';

import foto1 from '../img/foto1.jpeg.jpeg';
import foto2 from '../img/foto2.jpeg';
import './Load.css';

const Load = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="home"/>}
		>
			VK bot на node js
		</PanelHeader>

		<div><h1>1: Установите node js</h1></div>
		
		<div><h1>2: Я буду использовать редактор исходного кода: Visual Studio Code</h1></div>
		<div><h1>3: Создайте папку, у меня "Bot.test" После напишите в терминал"cd [Ваша папка]"  </h1></div>
		<div><h1>4: После напишите в терминале: npm i node-vk-bot-api -S </h1></div>
		<div><h1>5: После напишите в терминале: npm i node-vk-bot-api -S </h1></div>
		<div><h1>6: Создайте файл, у меня "node.js"</h1></div>
		<div><h1>7: В самом низу есть полный код!</h1></div>
   
		<img className="foto1" src={foto1} alt=""/>
		<div><h1>8: Запуск команды: node [Ваш файл], Это запустит нашего бота!</h1></div>
		<img className="foto2" src={foto2} alt=""/>


		


		<div id="openModal" class="modal">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h3 class="modal-title">Исходник</h3>
        <a href="#close" title="Close" class="close">×</a>
      </div>
      <div class="modal-body">    
        <h5> Исходник: https://vk.com/@lasvegasworldlv-bystryi-start-bot-node-js  </h5>
      </div>
    </div>
  </div>
</div>

<a href="#openModal"></a>

<Div>
       <Button href="#openModal" size="l">Код node js</Button>
	   
     </Div>


	 
	

	</Panel>
)




Load.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


export default Load;
