import React from 'react';
import PropTypes from 'prop-types';

import { Panel, Badge, Counter, PanelHeader, Header, TabbarItem, Button, Group, Tabbar, Cell, Div, Avatar, CellButton, Link, List, HeaderButton, platform, removeEventListener, PanelHeaderBack, FormItem, SliderSwitch, View } from '@vkontakte/vkui';


import { Icon28NewsfeedOutline } from '@vkontakte/icons';
import { Icon16Add } from '@vkontakte/icons';
import { Icon28GameOutline } from '@vkontakte/icons';
import { Icon28Settings } from '@vkontakte/icons';
import { Icon20CancelCircleFillRed } from '@vkontakte/icons';
import { Icon20CheckCircleFillGreen } from '@vkontakte/icons';
import { Icon28Newsfeed } from '@vkontakte/icons';
import { Icon28GhostOutline } from '@vkontakte/icons';

import persik from '../img/persik.png';
import './Help.css';

let a = 2;
const ColoredLine = ({ color }) => (
	<hr
		style={{
			color: color,
			backgroundColor: color,
			width: 750
			
		}}
	/>
  );

const Help = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="home"/>}
		>
			Каталог игр/VK coin.
		</PanelHeader>
		
    <div>
        <div><center><h1>{"Каталог"}</h1></center></div>
    </div>

	<Div>
		
       <Button href="https://vk.com/im?sel=-205779401"  mode="commerce" before={<Icon16Add/>}>ДОБАВИТЬ</Button>
     </Div>

	 

	 <ColoredLine color="" />


	 <Div style={{display: 'flex'}}>
       <Button size="l" href="https://vk.com/lasvegasworldlv" before={<Icon20CheckCircleFillGreen/>} stretched style={{ marginRight: 20 }}>Las Vegas</Button>
       <Button size="l" before={<Icon20CancelCircleFillRed/>} stretched mode="secondary" style={{ marginRight: 15 }} >Empty</Button>
     </Div>
	 <Div style={{display: 'flex'}}>
      
       <Button size="l" before={<Icon20CancelCircleFillRed/>} stretched mode="secondary" style={{ marginRight: 20 }} >Empty</Button>
	   <Button size="l" style={{ background: '#232323' }} before={<Icon20CancelCircleFillRed/>} stretched mode="secondary">Empty</Button>
     </Div>

	 <div class="border-style-solid2">Чтобы добавить свой проект, напишите нашей тех поддежке! {a}</div>

	

	<Tabbar>
      
	<TabbarItem onClick={props.go} data-to="home" text="Главная">
        <Icon28GhostOutline  />
      </TabbarItem>

      <TabbarItem onClick={props.go} data-to="Help" text="Каталог">
        <Icon28Newsfeed  />
      </TabbarItem>
	  <TabbarItem  onClick={props.go} data-to="Bas" indicator={<Counter size="s" ></Counter>} text="Бонусы">
        <Icon28GameOutline  />
      </TabbarItem>
      <TabbarItem onClick={props.go} data-to="Prof" indicator={<Badge mode="" aria-label="Новый раздел"  />} text="Настройки">
        <Icon28Settings />
      </TabbarItem>
</Tabbar>



	</Panel>
);




Help.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};




export default Help;