import React from 'react';
import PropTypes from 'prop-types';

import { Panel, Tabs, ConfigProvider, AdaptivityProvider, AppRoot, SimpleCell, PanelHeaderSubmit, Badge, PanelHeaderButton, Counter, PanelHeader, Header, TabbarItem, Button, Group, Tabbar, Cell, Div, Avatar, CellButton, Link, List, HeaderButton, platform, removeEventListener, PanelHeaderBack, FormItem, SliderSwitch, View } from '@vkontakte/vkui';

import { Icon28NewsfeedOutline } from '@vkontakte/icons';
import { Icon28MessageOutline } from '@vkontakte/icons';
import { Icon24Error } from '@vkontakte/icons';



import bridge from '@vkontakte/vk-bridge';
import persik from '../img/persik.png';
import './Pers.css';

bridge.send("VKWebAppInit");




const Pers = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="home"/>}
		>
			Информация!!
		</PanelHeader>
		<img className="Persik" src={persik} alt="Persik The Cat"/>
		
        <div>
        <div><center><h1>{"Error page"}</h1></center></div>
    </div>

	

	</Panel>
);




Pers.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};




export default Pers;
