import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, Snackbar, Tabbar, Avatar, TabbarItem, PanelHeaderBack, Panel, PanelHeader, Placeholder, Alert, ConfigProvider, ScreenSpinner, AdaptivityProvider, AppRoot } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';


import Home from './panels/Home';

import Persik from './panels/Persik';
import Help from './panels/Help';
import Load from './panels/Load';
import Pers from './panels/Pers';
import Vivod from './panels/Vivod';
import Test from './panels/Test';
import Prof from './panels/Prof';
import Bas from './panels/Bas';

import { Icon28Profile } from '@vkontakte/icons';

const App = () => {
	const [activePanel, setActivePanel] = useState('home', 'Prof');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	const [snackbarIsActive, setSnackbarIsActive] = useState(false)
	const [snackbar, setSnackbar] = useState(null);

const axios = require("axios");


const showLocationAlert = () => {
	setPopout(
		<Alert
			actionsLayout="vertical"
			actions={[{
				  title: "ОК",
				  autoclose: true,
				  mode: "cancel"
			}]}
			onClose={hideAlert}
		  >
			<h2>Не удалось</h2>
			<p>Пожалуйста, разрешите доступ к вашему местоположению</p>
		  </Alert>
	)
}
const hideAlert = () => {
	setPopout(null);
}
const allowNotifications = () => {
	if (fetchedUser) {
		setPopout(<ScreenSpinner size="large"/>);
		bridge.send("VKWebAppAllowNotifications", {})
			.then((data) => {
				setPopout(null);
				openNotificationsSnackbar();
			})
			.catch(() => {
				console.log("User declined getting notifications");
				setPopout(null);
			});
	}
}


const ShowNativeAds = () => {
	if (fetchedUser) {
		setPopout(<ScreenSpinner size="large"/>);
		bridge.send("VKWebAppShowNativeAds", {ad_format:"preloader"})
			.then((data) => {
				setPopout(null);
				openNotificationsSnackbar();
			})
			.catch(() => {
				console.log("User declined getting notifications");
				setPopout(null);
			});
	}
}

const goToNextView = e => {
	setActiveView(e.currentTarget.dataset.to);
};

const showEditAlert = () => {
	setPopout(
		<Alert
			actionsLayout="vertical"
			actions={[{
				  title: "ОК",
				  autoclose: true,
				  mode: "cancel"
			}]}
			onClose={hideAlert}
		  >
			<h2>Что это?</h2>
			<h4>VK mini app</h4>
		  </Alert>
	)
}

const openNotificationsSnackbar = () => {
	setSnackbarIsActive(true)
	setSnackbar(
		  <Snackbar
			layout="vertical"
			onClose={() => {setSnackbarIsActive(false); setSnackbar(null)}}
			before={<Avatar size={24} style={blueBackground}><Icon28Profile fill="#fff" width={14} height={14} /></Avatar>}
		  >
			Вы разрешили получение уведомлений!
		  </Snackbar>
	)
  }

const confirmLocation = () => {
	if (fetchedUser) {
		setPopout(<ScreenSpinner size="large"/>);
		bridge.send("VKWebAppGetGeodata", {})
			.then((data) => {
				usersRef.doc("id" + fetchedUser.id).update({
					lat: data.lat,
					long: data.long
				});
				setCancelLocation(false);
				setPopout(null);
				openLocationSnackbar();
			})
			.catch(() => {
				console.log("User declined location sharing");
				setCancelLocation(true);
				setPopout(null);
			});
	}
}



	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});

		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);



	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const goToNextPanel = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<AdaptivityProvider>
			<ConfigProvider scheme="bright_light">
			<AppRoot>
				<View activePanel={activePanel} popout={popout}>
					<Home id='home' snackbar={snackbar} showEditAlert={showEditAlert} ShowNativeAds={ShowNativeAds} allowNotifications={allowNotifications} confirmLocation={confirmLocation} goToNextPanel={goToNextPanel} showLocationAlert={showLocationAlert} fetchedUser={fetchedUser} go={go} />
					<Persik id='persik' go={go} />
					<Help id="Help" go={go} />
					<Load id="Load" go={go} />
					<Prof id='Prof' showEditAlert={showEditAlert} allowNotifications={allowNotifications} confirmLocation={confirmLocation} goToNextPanel={goToNextPanel} showLocationAlert={showLocationAlert} fetchedUser={fetchedUser} go={go} />
					<Pers id="Pers" go={go} />
					<Test id="Test" go={go} />
					<Bas id="Bas" allowNotifications={allowNotifications} confirmLocation={confirmLocation} goToNextPanel={goToNextPanel} showLocationAlert={showLocationAlert} go={go} />
					<Vivod id="Vivod" go={go} />
				
				</View>
			</AppRoot>
			</ConfigProvider>
		</AdaptivityProvider>
		
	
	);
}





export default App;